@import url(https://fonts.googleapis.com/css2?family=Balthazar&family=Mali&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #021626 !important;
  color: #FFFFFF !important;
}

.container {
  margin: 5px;
}

.upper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.capture {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px;
}

.capture video {
  margin-bottom: 10px;
}

.capture button {
  width: 150px;
  height: 41px;
  background-color: #9307e4c2;
  border: 1px solid rgb(41, 32, 33);
  border-radius: 4px;
  color: #FFF;
  font-weight: 900;
  cursor: pointer;
  outline: none;
}

.capture button:hover {
  background-color: #ca0fcaa6;
}

.results {
  margin-top: 40px;
}
.results .selected {
  border: 2px solid gold;
  -webkit-animation: moveIt 5s infinite;
          animation: moveIt 5s infinite;
}

@-webkit-keyframes moveIt {
  25% {border: 4px solid rgb(240, 202, 36);}
  50% {border: 4px solid rgb(211, 177, 24);}
  100% {border: 4px solid rgb(179, 150, 24);}
}

@keyframes moveIt {
  25% {border: 4px solid rgb(240, 202, 36);}
  50% {border: 4px solid rgb(211, 177, 24);}
  100% {border: 4px solid rgb(179, 150, 24);}
}

.results .MedicalWaste {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
}

.results .MedicalWaste li {
  padding: 15px 0px 0px 15px;
}

.results .MedicalWaste .img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  padding: 5px;
}

.results ul{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.results .MedicalWaste .name {
  margin: 0;
  padding: 0;
  text-align: center;
}

.logo{
    height: 15rem;
    width: 15rem;
    border-radius: 1rem;
}
.tabsbg{
    background-color: rgb(186, 248, 135);
    font-family: "Mali" , cursive !important;
}
@-webkit-keyframes side-to-side {
    from{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    }
    50%{
        -webkit-transform: translateX(10%);
                transform: translateX(10%);
    }
    to{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    }
}
@keyframes side-to-side {
    from{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    }
    50%{
        -webkit-transform: translateX(10%);
                transform: translateX(10%);
    }
    to{
        -webkit-transform: translateX(-10%);
                transform: translateX(-10%);
    }
}
.heads{
    -webkit-animation: side-to-side infinite 5s ease-in-out;
            animation: side-to-side infinite 5s ease-in-out;
}

